/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 20.5%;
							width: 79.5%;
							box-sizing: border-box;
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								box-sizing: border-box;
								width: 20%;
								padding: 0;
								
								a {
									width: 88%;
									border: 1px solid $default-border-color;
									box-sizing: border-box;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$topPos: ($i * 25 + $i)#{'%'};
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 103px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 54px;
        float: left;
        box-sizing: border-box;

		.mkdf-price-stock-holder {
			border-bottom: 1px solid $default-border-color;
			padding-bottom: 18px;
			font-size: 22px;
		}
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 32px 0 0;
			font-weight: 400;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 14px 0 3px;

			.star-rating {
				letter-spacing: 4px;
			}

			.woocommerce-review-link {
				color: $additional-text-color;
				font-size: 14px;
			}
	    }
	
	    .woocommerce-product-details__short-description {
			color: $additional-text-color;
			font-weight: 300;

		    @include mkdfRelativeHolderLayout();
		    margin: 8px 0 0;
		
		    p {
			    margin: 0;
		    }

			+ p.stock.in-stock,
			p.stock.out-of-stock {
				display: none;
			}
	    }
	
	    .product_meta {
		    margin: 32px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;
				font-size: 14px;
				font-weight: 500;
				color: $default-heading-color;
			
			    a,
				span {
					font-weight: 400;
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
			font-size: 14px;
			display: inline-block;
		    color: #00b489;
		    margin: 8px 0 0;
			padding-left: 20px;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 17px 0 31px;
	    }

		.mkdf-quantity-buttons {
			margin: 0 -6px 0 0;
		}

		button[type=submit].single_add_to_cart_button:not(.mkdf-woo-search-widget-button),
		.cart .button {
			border-radius: 0;
			padding: 10px 30px;
			font-size: 14px;
			font-weight: 500;
			text-transform: capitalize;
		}

	}
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 56px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;

				&:first-child {
					a {
						padding-left: 0;
					}
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 10px 21px;
					font-weight: 500;
				}
			}
		}

		.woocommerce-Tabs-panel {
			margin: 0 -1000px;
			padding: 0 1000px;
			background-color: #fdfdfd;
			border-bottom: 1px solid #f8edf0;
			border-top: 1px solid #f8edf0;
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 80px 0;
			}
		}
		
		table {
			margin: 80px 0;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
				font-size: 14px;
				font-weight: 500;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 104px 0;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;

					input,
					textarea {
						background-color: #fff;
					}
				}

				.comment-form-email {
					input[type=email] {
						width: 100%;
					}
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 74px 0 0px;
		
		> h2 {
			font-weight: 400;
			display: block;
			margin: 36px 0 50px;
			text-align: center;
		}
		
		.product {

			.mkdf-products-inner {
				padding: 10px 15px 20px;
			}
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */