/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.mkdf-woo-pagination-holder {
    @include mkdfRelativeHolderLayout();
    margin: 0 0 107px;
    clear: both;

    .mkdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: center;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0;

            a,
            span {
                position: relative;
                display: inline-block;
	            width: auto;
                margin: 0 8px;
                padding: 0;
                font-size: 19px;
                vertical-align: middle;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                color: #adaeb0;
                font-weight: 500;
                @include mkdfTransition(color .2s ease-out);

                &:hover,
                &.current {
                    color: $first-main-color;
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 1;
                    margin: 0 6px 0 0;

                    &:before {
                        color: #8e8e8e;
                        position: relative;
                        top: -1px;
                        display: block;
                        font-family: "dripicons-v2" !important;
                        font-size: 19px;
                        line-height: inherit;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                    }
                }
    
                &.prev {
        
                    &:before {
                        content: "\55";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\56";
                    }
                }

                &:hover {
                    &.next,
                    &.prev {

                        &:before {
                            color: $first-main-color;
                        }
                    }
                }
            }
        }
    }
}

.mkdf-woo-view-all-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 1%;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 2px;
        color: $default-text-color;

        &:before {
            content: '|';
            padding: 0 14px 0 0;
            color: $default-text-color;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */