/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.mkdf-content .woocommerce:not(.widget) {
    @include mkdfRelativeHolderLayout();
}

.mkdf-pl-main-holder {
    margin: 0 0 39px;
    
    .mkdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
    margin: 0 -10px;
    clear: both;
    overflow: hidden;

    > .product {
        position: relative;
        float: left;
        display: inline-block;
        vertical-align: middle;
        padding: 0 10px;
        margin: 0 0 20px;
        box-sizing: border-box;

        .mkdf-products-inner {
            border: 1px solid $default-border-color;
            padding: 10px 15px;
            @include mkdfTransition(all 0.15s ease-out);

            &:hover {
                box-shadow: 2px 2px 25px 0 rgba(255,51,102,.07);
                @include mkdfTransition(all 0.2s ease-out);
            }

            > a {
                color: $additional-text-color;
                font-size: 14px;
                font-weight: 400;
                display: inline-block;
                vertical-align: middle;

                &:hover {
                    color: $first-main-color;
                }
            }

            .yith-wcwl-add-to-wishlist {
                float: right;

                & + div {
                    display: none;
                }

                .yith-wcwl-add-button a:after,
                .yith-wcwl-wishlistaddedbrowse a:after,
                .yith-wcwl-wishlistexistsbrowse a:after {
                    padding-right: 0;
                }
            }

            .compare-button {
                width: auto;
                display: inline-block;
                float: right;
            }

            .compare-button .compare {
                font-size: 0;
                background-color: transparent;
                padding: 0;

                &:hover {
                    background-color: transparent;

                    &:after {
                        color: $first-main-color;
                    }
                }

                &.added {
                    color: $first-main-color;
                    display: inline-block;

                    &:after {
                        content: "\e040";
                        color: $first-main-color;
                        font-family: "dripicons-v2" !important;
                        font-size: 16px;
                        padding: 0;
                    }
                }

                &:after {
                    content: "\e040";
                    color: $additional-text-color;
                    font-family: "dripicons-v2" !important;
                    font-size: 16px;
                    padding: 0;
                }
            }

            .touch & {
                cursor: pointer;
            }

            .mkdf-product-list-title {
                margin: 7px 0 0px;
            }

            .mkdf-pl-rating-holder {
                position: absolute;
                display: block;
                width: 100%;
                top: 0;
                left: 0;
                line-height: 1;
                margin: 10px 0 3px;

                .star-rating {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    font-family: "ElegantIcons";
                    font-size: 13px;
                    line-height: 1;
                    letter-spacing: 5px;
                    color: #d2d2d2;
                    overflow: hidden;
                    -webkit-font-smoothing: antialiased;

                    &:before {
                        content: "\e033\e033\e033\e033\e033";
                    }

                    span {
                        position: absolute;
                        top: 0;
                        left: 0;
                        overflow: hidden;

                        &:before {
                            content: "\e033\e033\e033\e033\e033";
                            color: #f8e900;
                        }
                    }
                }
            }

            .price {
                line-height: 1;
                margin: 2px 0 0;
                font-size: 22px;
                color: $default-heading-color;
                display: block;

                * {
                    font-size: inherit;
                    line-height: inherit;
                }

                del {
                    margin: 0 8px 0 0;
                    font-size: 16px;
                    color: $additional-text-color;
                    font-weight: 500;
                    vertical-align: middle;
                    display: inline-block;
                }

                ins {
                    color: $first-main-color;
                    text-decoration: none;
                    vertical-align: middle;
                    display: inline-block;
                }
            }


            .mkdf-pl-inner {
                @include mkdfRelativeHolderLayout();
                overflow: hidden;

                .mkdf-pl-image {
                    @include mkdfRelativeHolderLayout();
                    overflow: hidden;
                    padding-top: 35px;

                    .mkdf-labels-holder {
                        position: absolute;
                        display: block;
                        top: 0;
                        width: auto;

                        .mkdf-pl-new-product,
                        .mkdf-pl-onsale {
                            position: relative;
                            display: inline-block;
                            top: 0;
                            width: auto;
                            height: 24px;
                            color: #ff4b4b;
                            font-size: 14px;
                            font-weight: 500;
                            line-height: 24px;
                            letter-spacing: 0;
                            text-transform: capitalize;
                            padding: 0;
                            margin: 0;
                            text-align: center;
                            z-index: 100;
                            box-sizing: border-box;
                        }

                        .mkdf-pl-onsale {
                            background: transparent;
                        }
                    }

                    img {
                        display: block;
                        width: 100%;
                    }
                }

                > a {
                    @include mkdfAbsoluteHolderLayout();
                }

                &:hover {
                    + .mkdf-pl-text-wrapper h5 > a {
                        color: $first-main-color;
                    }
                }
            }

            .mkdf-pl-text-wrapper {
                @include mkdfRelativeHolderLayout();
                margin: 0 0 10px;
                text-align: left;
                display: table;
                height: 100%;
                padding-top: 26px;

                .mkdf-pl-text-left-holder {
                    display: block;
                    position: relative;
                }

                .mkdf-pl-add-to-cart-holder {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    overflow: hidden;

                    .button {
                        font-family: inherit;
                        position: relative;
                        vertical-align: middle;
                        outline: 0;
                        line-height: 20px;
                        font-weight: 500;
                        text-transform: capitalize;
                        box-sizing: border-box;
                        margin: 0;
                        @include mkdfTransition(color .2s ease-in-out,background-color .2s ease-in-out,border-color .2s ease-in-out);
                        color: #fff;
                        border: 2px solid transparent;
                    }

                    .product_type_grouped {
                        font-size: 0;
                        border-radius: 50%;
                        padding: 0;
                        width: 43px;
                        height: 43px;
                        position: relative;
                        background-color: #f9f9f9;

                        &:after {
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%, -50%);
                            font-family: "dripicons-v2" !important;
                            content: "\e037";
                            font-size: 18px;
                            color: #8e8e8e;
                        }
                        &:hover {
                            background-color: $first-main-color;
                            &:after {
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }

        .button.added {
            display: none;
        }

        .added_to_cart {
            font-size: 0;
            border-radius: 50%;
            padding: 0;
            width: 43px;
            height: 43px;
            position: relative;

            &:after {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                content: "\53";
                font-family: "dripicons-v2" !important;
                font-size: 18px;
            }
        }

        .add_to_cart_button, .product_type_external {
            font-size: 0;
            border-radius: 50%;
            padding: 0;
            width: 43px;
            height: 43px;
            position: relative;
            background-color: #f9f9f9;

            &:before {
                content: "\6b";
                font-family: "dripicons-v2" !important;
                position: absolute;
                top: 0;
                left: 47%;
                color: #fff;
                font-size: 9px;
                text-transform: none !important;
                opacity: 0;
                @include mkdfTransform(translate(-47%, -100%));
                -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
                transition: transform .35s cubic-bezier(.53,.01,.18,1), opacity .3s ease;
            }

            &:after {
                position: absolute;
                top: 50%;
                left: 47%;
                @include mkdfTransform(translate(-47%, -50%));
                content: "\51";
                font-family: "dripicons-v2" !important;
                font-size: 18px;
                color: #8e8e8e;
                -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                transition: transform .35s cubic-bezier(.53,.01,.18,1);
            }
            &:hover {

                &:before {
                    @include mkdfTransform(translate(-30%, 0));
                    opacity: 1;
                    color: #fff;
                }

                &:after{
                    color: #fff;
                    @include mkdfTransform(translate(-50%, -40%));
                    -webkit-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                    -moz-transition: transform .35s cubic-bezier(.53,.01,.18,1);
                    transition: transform .35s cubic-bezier(.53,.01,.18,1);
                }
            }


        }

        .product_type_external {
            &:before {
                content: "";
            }

            &:after {
                left: 48%;
                @include mkdfTransform(translate(-48%, -50%));
                content: "\6d";
                text-transform: none;
            }

            &:hover {
                &:after {
                    color: #fff;
                    @include mkdfTransform(translate(-40%,-50%));
                }
            }
        }

        &.outofstock {

            .mkdf-pl-inner {

                .mkdf-pl-image {
                    img {
                        opacity: .1;
                    }

                    .mkdf-out-of-stock {
                        position: absolute;
                        top: 56%;
                        left: 50%;
                        vertical-align: middle;
                        transform: translate(-50%, -56%);
                        right: auto;
                        background: transparent;
                        display: block;
                        width: auto;
                        height: 24px;
                        color: #ff4b4b;
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 24px;
                        letter-spacing: 0;
                        text-transform: capitalize;
                        padding: 0;
                        margin: 0;
                        text-align: center;
                        z-index: 100;
                        box-sizing: border-box;
                    }
                }

                &:hover {
                    + .mkdf-pl-text-wrapper h5  >a {
                        color: $default-heading-color;
                    }
                }
            }

            .mkdf-pl-text-wrapper {

                .button {
                    font-size: 0;
                    border-radius: 50%;
                    padding: 0;
                    width: 43px;
                    height: 43px;
                    position: relative;
                    background: #f9f9f9;

                    &:after {
                        color: #ff4b4b;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        content: "\e067";
                        font-family: "dripicons-v2" !important;
                        font-size: 18px;
                    }
                }
            }
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.mkdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.mkdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */